@import "../../helpers/variables";
@import "../../helpers/mixins";

.feedback {
  margin-top: $level-2;
  @include adaptive(1366) {
    margin-top: $level-3;
  }
  @include adaptive(768) {
    display: flex;
    flex-direction: column;
  }
  &__wrapper {
    pointer-events: none;
  }
  &__form {
    width: calc(50% - 15px);
    height: 574px;
    background: $orange;
    padding: $level-3 95px;
    pointer-events: initial;
    @include adaptive(1600) {
      padding: $level-4 80px;
      width: calc(50% - 10px);
      height: 480px;
    }
    @include adaptive(1200) {
      width: calc(50% - 5px);
      height: 499px;
      padding: $level-4 70px;
    }
    @include adaptive(768) {
      width: 100%;
      height: 445px;
      padding: $level-5;
      order: 1;
    }
    @include adaptive(600) {
      height: 464px;
    }
    @include adaptive(420) {
      height: 502px;
    }
    @include adaptive(420) {
      height: 557px;
    }
    @include adaptive(360) {
      height: 576px;
    }
    &_header {
      margin-bottom: 20px;
    }
    &_text {
      font-size: 16px;
      padding-right: 60px;
      margin-bottom: $level-4;
      @include adaptive(1600) {
        margin-bottom: $level-5;
      }
    }
    &_fields {
      width: 100%;
      margin-bottom: $level-4;
      @include adaptive(1600) {
        margin-bottom: $level-5;
      }
    }
    &_input {
      width: 100%;
      margin-bottom: 32px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &_policy {
      font-size: 13px;
      margin-bottom: $level-4;
      @include adaptive(1600) {
        margin-bottom: $level-5;
      }
      & a {
        color: $black;
        font-weight: 400;
        text-decoration: underline;
        &:hover {
          text-decoration: none;
        }
      }
    }
    &_button {
      width: 220px;
      height: 64px;
      @include adaptive(1600) {
        height: 48px;
      }
      @include adaptive(420) {
        width: 100%;
      }
    }
  }
  &__map {
    width: 100%;
    height: 762px;
    position: absolute;
    bottom: 0;
    display: block;
    @include adaptive(1600) {
      height: 660px;
    }
    @include adaptive(1366) {
      height: 620px;
    }
    @include adaptive(1024) {
      height: 640px;
    }
    @include adaptive(768) {
      position: relative;
      height: 445px;
      order: 2;
    }
    @include adaptive(600) {
      height: 300px;
    }
  }
}
